











import { Component, Inject, Vue, Watch } from 'vue-property-decorator'
import { TranslationService, UseCaseFactory } from '../../application'
import { Article, Id } from '../../domain'
import { State, VueStateManager } from '../state'
import Prism from 'prismjs'

@Component<ArticleComponent>({
  async beforeRouteEnter(to, _from, next) {
    if (to.name === undefined) {
      return
    }

    const article = await UseCaseFactory.get<Article>('GetArticle', {
      id: Id.fromValue(to.params.id),
      locale: VueStateManager.instance.state.locale
    }).execute()

    next(vm => {
      vm.article = article
    })
  }
})
export default class ArticleComponent extends Vue {
  article: Article | null = null

  @Inject()
  readonly state!: State

  @Inject()
  readonly translationService!: TranslationService

  @Watch('state.locale')
  async onLocaleChange() {
    this.article = await UseCaseFactory.get<Article>('GetArticle', {
      id: Id.fromValue(this.$route.params.id),
      locale: VueStateManager.instance.state.locale
    }).execute()
  }

  mounted() {
    Prism.highlightAll()
  }

  get articleLocale() {
    return this.translationService.toString(this.article!.locale)
  }

  get title() {
    return this.article!.title
  }

  get body() {
    return this.article!.body.toHtml()
  }
}
